

.container{
	display: flex;
    flex-direction: column;
    /* align-items: center;  */
    width: 100%;
    height: 100vh;
    /* padding: 20px 30px 0px 30px; */
	background-color: aliceblue;
    /* margin: 0px auto 20px auto; */
    /* background-color: ; */
    /* overflow-y: scroll; */
    /* y축 스크롤 바 가리기 */
    /* -ms-overflow-style: none; */
	/* scrollbar-width: none; */
}
/* .container::-webkit-scrollbar{
	display: none;
} */
.container p{
	margin-top: 10px;
	margin-left: 50px;
	/* display: flex; */
	width: 90%;
	font-family: 'SUITE-Regular';
	font-size: 25px;
}
.postList{
	box-sizing: border-box;
	width: 90%;
    height: 100%;
	display: flex;
	flex-direction: column;
	/* justify-content:flex-start; */
	background-color: rgb(255, 255, 255);
	border-radius: 5px;
	padding: 15px;
    /* overflow: hidden; */
	margin: 10px auto;
	background-color: white;
	max-height: 100%; 
	/* 포스트 리스트의 최대 높이 설정 */
	/* overflow-y: scroll; */
}
.postItem{
	background-color: rgba(255, 248, 196, 0.771);
    width: 100%;
    height: 95px;
    font-size: 15px;
    /* border: 2px solid red; */
    border-radius: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
	padding: 10px;
    text-align: start;
    display: flex;
	cursor: pointer;
	font-family: 'SUITE-Regular';
	position: relative;
	z-index: 1; /* 상위 컨테이너를 가리지 않도록 설정 */
}
.postItem .numIdx{
	width: 30px;
	height: 80px;
	text-align: center;
	font-size: 20px;
	margin-right: 5px;

}
.postItem .contents{
	display: flex;
    justify-content: space-between;
}
.contents p:nth-child(1){
	font-size: 20px;
}
.postTitle{
	font-size: 18px;
}
.postContent{
	margin-top: 10px;
	font-size: 20px;
}
.postUserId{
    /* background-color: darkcyan; */
    font-size: 14px;
    width: 60px;
    margin-bottom: 10px;
}
.postUserId p{
    margin-right: 10px;
    /* background-color: darkgreen; */
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: gray;
}
.participation{
	display: flex;
    justify-content: space-between;
	align-items: end;	

}
.participation .participationItem{
	display: flex;
    justify-content: space-between;
	margin-top: 10px;
    width: 90px;
    /* background-color: darkgoldenrod; */
}
@media screen and (max-width: 700px){
	.container{
		width: 100%;
		height: 100vh;
	}
	.postList{
		box-sizing: border-box;
		width: 90%;
		height: 100%;
		display: flex;
		flex-direction: column;
		/* justify-content: center; */
		background-color: rgb(255, 255, 255);
		border-radius: 5px;
		padding: 15px;
		/* overflow: hidden; */
		margin: 10px auto;
		background-color: white;
		max-height: 100%; 
		/* 포스트 리스트의 최대 높이 설정 */
		/* overflow-y: scroll; */
	}
}